import React from 'react'
import Slider from "react-slick"
import SectionTitle from "../sectionTitle/sectionTitle"

const Awards = props => {
    var settings = {
        dots: false,
        infinite: true,
        speed: 1000,
        slidesToShow: 4,
        lazyLoad: 'progressive',
        slidesToScroll: 4,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                }
            },
            {
                breakpoint: 1000,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    dots: true
                }
            },
            {
                breakpoint: 800,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    dots: true
                }
            },
            {
                breakpoint: 550,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: false,
                    rows: 2,
                    dots: true
                }
            },
        ]
    }
    return (
        <div className="awards-section grid">
            <picture>
                <source media="(max-width: 500px)" srcset="./Assets/images/bg-sm.jpg" />
                <source media="(max-width: 1200px)" srcset="./Assets/images/bg-m.jpg" />
                <img src="../Assets/images/bg.jpg" className="awards-bg" />
            </picture>
            <SectionTitle section={"Timeline"} title={props.whos} subtitle={"Awards"} />
            <div className="awards">
                <Slider {...settings} className="awards-slider">
                    {props.awards.map((edge, index) => {
                        return <div className="award-col" key={index}>
                            <div className="award"><img src={edge.file.url} /></div>
                            <h1>{edge.title}</h1>
                        </div>
                    })}
                </Slider>
            </div>
        </div>
    )
}


export default Awards